.message {
    background: #3674FF;
    box-shadow: 0px 5px 5px rgba(54, 116, 255, 0.196733);
    border-radius: 12px 12px 12px 0px;
    padding: 14px 15px 10px 15px;
}

.avatar > img {
    width: 30px;
    height: 33.75px;
    border-radius: 50%;
}

.avatar {
    padding-top: 3%;
    padding-right: 1%;
}

.alterMessage {
    background: #FFFFFF;
    border: 1px solid #ECECEC;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.0220444);
    border-radius: 12px 12px 0px 12px;
    padding: 14px 15px 10px 15px;

}

.message > h1 {
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    color: white;
}

.message > p {
    color: snow;
}

.alterMessage > h1 {
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    color: blueviolet;
}

.alterMessage > h1, p {
    margin: 0px;
}

.messageBubble {
    display: flex;
}

.alterTime {
    display: flex;
    justify-content: flex-end;
    padding-top: 5%;
    padding-right: 5%;
}

.alterAvatar {
    padding-top: 12%;
    padding-left: 3%;
}

.alterAvatar > img {
    width: 30px;
    height: 33.75px;
    border-radius: 50%;
}

.alterMessageBubble {
    display: flex;
    flex-direction: row-reverse;
    max-width: 30%;

}

h1, p {
    margin: 0px;
    font-size: 100%;
}

time {
    font-family: monospace;
}